/**
 * history
 */


 export default {

    go: function(num) {
        window.history.go(num);
    },
   
    back: function(num) {
        window.history.back(num);
    },
   
    forward: function() {
        window.history.forward();
    },
    /**
     * pushStatehistory
     * @param {object} state pushState()javascriptpopstatestatestatenull
     * @param {string} title null
     * @param {string} url  URLURLpushState()URL
     */
    pushState: function(state, title, url) {
        if (title) {
            document.title = title;
        }
        window.history.pushState(state, title, url);
    },
   
    /**
     * replaceState()
     * @param {object} state 
     * @param {string} title 
     * @param {string} url 
     */
    replaceState: function(state, title, url) {
        if (title) {
            document.title = title;
        }
        window.history.replaceState(state, title, url);
    },
   
   /**
    * (history)popstate
    * pushStatereplaceStatejavascriptback()forward()go()
    */
    popstate: function(callback) {
        window.onpopstate = (e) => {
            console.log('state changed!!!');
            callback && callback(e);
        }
    },
    /**
     * hashchangehtml5apihash
     * @param {function} callback 
     */
    hashchange: function(callback) {
        window.onhashchange = (e) => {
            console.log('new url', e.newURL);
            console.log('old url', e.oldURL);
            callback && callback(e);
        }
    }
 }